.displayImage {
	height: auto;
	width: auto;
	margin-bottom: 20px;
	max-width: 75%;
	background-image: url(/images/irongrip.png);
	border: 1px solid #474747;
}

hr {
	border-top: 0px solid;
	max-width: 85%;
}

.galleryDescription {
	margin-top: -8px;
}

@media (max-width: 991px) {

    hr {
		border-top: 1px solid;
	}

}


